<script>
export default {
    watch: {
        valueBRL() {
            if (this.valueBRL == null) {
                setTimeout(() => {
                    this.valueBRL = 0
                }, 0)
            }
            if (this.currentTab == 'buy') {
                this.valueBTC = this.valueBRL / this.marketInfo.orderMarketBuyPrice
            } else {
                this.valueBTC = this.valueBRL / this.marketInfo.orderMarketSellPrice
            }
        },
        valueBTC() {
            if (this.valueBTC == null) {
                setTimeout(() => {
                    this.valueBTC = 0
                }, 0)
            }
            if (document.activeElement != this.$refs['brl-input'].$el) {
                if (this.currentTab == 'buy') {
                    this.valueBRL = this.marketInfo.orderMarketBuyPrice * this.valueBTC
                } else {
                    this.valueBRL = this.marketInfo.orderMarketSellPrice * this.valueBTC
                }
            }
        }
    },
    data() {
        return {
            currentTab: 'buy', // buy | sell
            valueBRL: 0.0,
            valueBTC: 0.0,
            marketInfo: {},
        }
    },
    methods: {
        setup() {
            fetch('https://api.upcambio.com/Info/topbar_info', {
                method: 'GET'
            }).then(response => {
                response.json().then(data => {
                    this.marketInfo = data.DATA
                })
            })
        }
    },
    mounted() {
        this.setup()
    }
}
</script>

